    getNews(ch_id) {
        // fetching news
         console.log('current date: ', formatDate(new Date(),'yyyy-MM-dd','en_US'));
       
        // (YYYY, MM, DD) 
      //  var g2 = new Date(2021, 08, 03);
      //console.log('new date is: ', g1);
        this.loader.presentLoading().then( () => {
        this.apiService.get_news_wall(ch_id).subscribe((res: any) => {
            this.loader.stopLoading();
            if (res.status) {
                this.news = res.newswall;
                if (this.news.length > 0) {
                    this.news.map(x => {
                        if(x.photo_profile.includes("https://") || x.photo_profile.includes("http://")){
                            this.live_url = true;
                        } else {
                            x.photo_profile = x.photo_profile ? this.dom.bypassSecurityTrustUrl(this.IMAGE_BASE_URL + x.photo_profile) : '';
                        }
                        if (x.attachment) {
                            if (x.mime_type === 'video' || x.attachment.includes('data:video')) {
                                x['video'] = true;
                            } else {
                                x['video'] = false;
                            }
                             
                            if( x.attachment !== "" && x.attachment !== null && x.attachment.includes("s3.wasabisys.com")  &&  x['video'] === false && formatDate(x.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29' ){

                              
                                let abc =x.attachment.split("/");
                                //  let img_name = abc[abc.length-1];
                                  let folder_name = abc[abc.length-2];
                                if(this.platform.is("mobile")){
                                    this.deviceType = 'radiobase_mobile';
                                }else if(this.platform.is("tablet")){
                                    this.deviceType = 'radiobase_tablet';
                                }else{
                                    this.deviceType = folder_name;
                                }
        
                                let new_link = x.attachment.replace(folder_name, this.deviceType);
                                x.attachment = new_link; 
                               // console.log('background is: ',card.background,'new link is: ', new_link);
                            }
                            // sanitizing attachment url for security purpose
                            x.attachment = this.dom.bypassSecurityTrustResourceUrl(x.attachment);

                        }
                        // reversing news data so that latest news comes on top
                        x.comments = x.comments.slice().reverse();
                        x.comments.map((item)=>{
                            try{
                                item.photo_profile =  item.photo_profile ? this.dom.bypassSecurityTrustUrl( item.photo_profile) : '';
                                item.comments = JSON.parse(item.comments);
                            } catch {
                                item.comments = item.comments;
                            }
                            let localTimeZone = moment.tz.guess(true);
                            item.comment_time = moment(item.comment_time).tz(localTimeZone);
                            item.comment_time = this.appService.getDateTimeFormat(item.comment_time);
                         });
                         if(x.created_at){
                            let mytime = moment(x.created_at);
                            let time_diff = moment().utcOffset();  // in minutes
                            if(time_diff != 0){
                                x.created_at = moment(mytime).add((time_diff), 'minutes');
                                x.created_at = this.appService.getDateTimeFormat(x.created_at);
                            } else {
                                x.created_at = this.appService.getDateTimeFormat(x.created_at);
                            }
                         }
                    });

                    //========= posting PINNED news on TOP =================
                    let pinned = this.news.filter( x => x.pin === 'Yes');
                    let unpinned = this.news.filter( x => x.pin !== 'Yes');
                    this.news = pinned.reverse().concat(unpinned.reverse());
                    //========= posting PINNED news on TOP =================

                    this.news.forEach(async _new => {
                        _new.description =_new.description != null ? await this.dom.bypassSecurityTrustHtml(this.urlify(_new.description)) : '';
                    });
                }
            }
            this.loader.stopLoading();
            this.dataLoaded = true;
        }, (err: any) => {
            let message = err.message;
            this.loader.stopLoading();
            this.dataLoaded = true;
            this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
        });
        })

        setTimeout(() => {
            this.loader.stopLoading();
        }, 2000);
    }

