    urlify(text) {
        var urlRegex = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig;
        if(this.platform.is('cordova')){
            let str = text;
            text.replace(urlRegex, (url) => {
                str = '';
                str = text.replace(urlRegex, `<a href="${url}" target="_blank" onclick="openInNewBrowser('${url}', this)">${url}</a>`);
            });
            return str;
        } else {
            return text.replace(urlRegex, function(url) {
                return `<a href="${url}" target="_blank">${url}</a>`;
            });
        }
    }

    getPicture(opts){
        this.camera.getPicture(opts).then((imageData) => {
            imageData = 'data:image/jpeg;base64,' + imageData;

             this.attachment = imageData ? imageData : "";
            if(this.attachment.includes('data:video')){
                this.mime_type = 'video';
            } else {
                this.mime_type = 'image';
            }
            // this.type = 'camera';
        }, (err) => {
            console.log("ERROR", err);
            // Handle error
        });
    }

    getCameraOptions(sourceType){
        if (sourceType === 'camera'){
            this.cameraOptions.sourceType = this.camera.PictureSourceType.CAMERA;
        } else if (sourceType === 'library'){
            this.cameraOptions.sourceType = this.camera.PictureSourceType.PHOTOLIBRARY;
        }

        return this.cameraOptions;
    }

    async libraryPopover() {
        const modal = await this.modalCtrl.create({
            component: ImagesLibraryComponent,
            cssClass: 'BibleModal',
            componentProps: {
            }
        });
        modal.onDidDismiss().then(res => {
            if (res.data.selected_image){
                let image_data = res.data.selected_image;

                this.attachment =image_data.url;
                // this.profile_picture_file = image_data.filename;
                 this.type = 'photo_library';
                this.mime_type = 'image';
            }
        });
        return await modal.present();
    }


    async presentActionSheet() {
        let buttons =  [
            {
                text: 'Camera',
                icon: 'camera',
                handler: () => {
                    let options = this.getCameraOptions('camera');
                    this.getPicture(options);
                }
            },{
                text: 'Upload',
                icon: 'folder',
                handler: () => {
                        let options = this.getCameraOptions('library');
                        this.getPicture(options);
                }
            },
            {
                text: 'Library',
                icon: 'images',
                handler: () => {
                    this.libraryPopover();

                    // let options = this.getCameraOptions('library');
                    // this.getPicture(options);
                }
            },{
                text: 'Cancel',
                icon: 'close',
                role: 'cancel',
                handler: () => {
                }
            }
        ];

        let remove_button = {
            text: 'Remove',
            role: 'destructive',
            icon: 'trash',
            handler: () => {
                this.removeAttachment();
            }};

        if (this.attachment){
            buttons.unshift(remove_button);
        }

        const actionSheet = await this.actionSheetController.create({
            cssClass: 'my-custom-class',
            buttons: buttons
        });
        await actionSheet.present();

        const { role } = await actionSheet.onDidDismiss();
        console.log('onDidDismiss resolved with role', role);
    }

